:root {
    --clr-primary: #ffad63;
    --clr-danger: #ff7782;
    --clr-success: #41f1b6;
    --clr-white: #fff;
    --clr-info-dark: #7d8da1;
    --clr-info-light: #dce1eb;
    --clr-dark: #363949;
    --clr-warning: #ff4edc;
    --clr-light: rgba(132, 139, 200, 0.18);
    --clr-primary-variant: #111e88;
    --clr-dark-variant: #677483;
    --clr-color-background: #f5f5f5;
    --card-border-radius: 2rem;
    --border-radius-1: 0.4rem;
    --border-radius-2: 0.8rem;
    --border-radius-3: 1.2rem;
    --card-padding: 1.8rem;
    --padding-1: 1.2rem;
    --box-shadow: 0 2rem 3rem var(--clr-light);
  
  }
  
  .dark-theme-variables {
    --clr-color-background: #181a1e;
    --clr-white: #202528;
    --clr-light: rgba(0, 0, 0, 0.4);
    --clr-dark: #edeffd;
    --clr-dark-variant: #677483;
    --box-shadow: 0 2rem 3rem var(--clr-light)
  }
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    border: none;
    list-style: none;
    /* appearance: none; */
  }
  
  
  .seller-sidebar-glc {
    height: 100%;
  
    body,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      margin: 0;
    }
  
    html {
      scroll-behavior: smooth;
    }
  
    img {
      display: block;
      max-height: 100%;
      max-width: 100%;
      margin: 0;
      height: auto;
      vertical-align: top;
    }
  
    /* Inherit fonts for inputs and buttons */
    input,
    button,
    textarea,
    select {
      font: inherit;
    }
  
    p {
      margin: 0 0 1em;
    }
  
    @media (prefers-reduced-motion: reduce) {
      * {
        animation-duration: 0.01ms !important;
        animation-iteration-count: 1 !important;
        transition-duration: 0.01ms !important;
        scroll-behavior: auto !important;
      }
    }
  
    .sidenav {
      display: flex;
      flex-flow: column nowrap;
      background: #ffad63;
      color: #fff;
      margin: 13px 0;
      padding: 1.5rem;
      border-radius: 1rem;
      flex: 0 1 auto;
      height: 95vh;
      max-width: 100%;
      transition: width 250ms ease-out;
      position: sticky;
      top: 1rem;
    }
  
  
    .sidenav.is-collapsed {
      width: 5.5rem;
    }
  
    .sidenav__header {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      position: relative;
    }
  
    .header__icon {
      height: 2.5rem;
      width: 2.5rem;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      flex: 0 0 auto;
    }
  
    .header__icon .fa {
      aspect-ratio: 1;
      font-size: 1.25rem;
    }
  
    .header__icon .fa {
      font-size: 1.75rem;
    }
  
    .header__text {
      display: inline-block;
      flex: 1;
      text-align: left;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      transition-property: opacity, transform;
      transition-duration: 250ms;
      transition-timing-function: ease-out;
      transition-delay: 100ms;
      font-weight: 700;
      flex: 1;
    }
  
    .is-collapsed .header__text {
      opacity: 0;
      transform: translateX(1rem);
      transition-delay: 0s;
    }
  
    .sidenav__button {
      display: inline-flex;
      flex-flow: row nowrap;
      align-items: center;
      padding: 0;
      background: none;
      color: #a7bbeb;
      border: 0;
      overflow: hidden;
      border-radius: 0.5rem;
      cursor: pointer;
      outline-offset: -2px;
      transition-property: background, color;
      transition-duration: 250ms;
      color: #fff;
      flex: 0 0 auto;
      position: absolute;
      left: 100%;
    }
  
    .is-active .sidenav__button {
      color: #fff;
    }
  
    .sidenav__button:focus-visible {
      outline: 2px solid #2c5bd0;
    }
  
    .sidenav__button:focus-visible,
    .sidenav__button:hover {
      background: #0d43c9;
      color: #fff;
    }
  
    .sidenav__button-icon {
      height: 2.5rem;
      width: 2.5rem;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      flex: 0 0 auto;
    }
  
    .sidenav__button-icon .fa {
      aspect-ratio: 1;
      font-size: 1.25rem;
    }
  
    .sidenav__button-text {
      display: inline-block;
      flex: 1;
      text-align: left;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      transition-property: opacity, transform;
      transition-duration: 250ms;
      transition-timing-function: ease-out;
      transition-delay: 100ms;
      font-weight: 700;
      flex: 1;
    }
  
    .is-collapsed .sidenav__button-text {
      opacity: 0;
      transform: translateX(1rem);
      transition-delay: 0s;
    }
  
    .header .sidenav__button-icon {
      width: auto;
      color: #fff;
    }
  
    .header .sidenav__button-icon>* {
      transition-property: transform, color;
      transition-duration: 250ms;
    }
  
    .header .sidenav__button-icon>*:hover {
      color: #fff;
    }
  
    .is-collapsed .header .sidenav__button-icon>* {
      transform: rotate(180deg);
    }
  
    .search {
      display: flex;
      flex-flow: row nowrap;
      position: relative;
      margin-top: 1rem;
    }
  
    .search__icon {
      height: 2.5rem;
      width: 2.5rem;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      flex: 0 0 auto;
      position: absolute;
      right: 0;
      top: 0;
      cursor: pointer;
    }
  
    .search__icon .fa {
      aspect-ratio: 1;
      font-size: 1.25rem;
    }
  
    .search__input {
      flex: 1 1 auto;
      min-width: 1px;
      height: 2.5rem;
      border: none;
      background: #0d43c9;
      border-radius: 0.5rem;
      color: #fff;
      padding: 0 2.5rem 0 1rem;
      outline-offset: -2px;
      transition-property: background, color, padding;
      transition-duration: 250ms;
    }
  
    .search__input:placeholder {
      color: #a7bbeb;
      transition-property: color;
      transition-duration: 250ms;
    }
  
    .search__input:focus {
      outline: 2px solid rgba(255, 255, 255, 0.3);
    }
  
    .is-collapsed .search__input {
      color: transparent;
      padding-left: 0;
    }
  
    .is-collapsed .search__input:not(:hover) {
      background: none;
    }
  
    .is-collapsed .search__input:placeholder {
      color: transparent;
    }
  
    .sidenav__nav {
      flex: 1 1 auto;
      margin: 1rem -1.375rem 0;
      padding: 0 1.375rem;
    }
  
    .sidenav__nav {
      overflow: hidden;
      overflow-y: auto;
    }
  
    @media (pointer: fine) {
      .nav {
        scrollbar-color: #a7bbeb transparent;
        scrollbar-width: thin;
        transition: scrollbar-color 200ms;
      }
  
      .nav::-webkit-scrollbar {
        height: 8px;
        width: 8px;
      }
  
      .nav::-webkit-scrollbar-track {
        background: transparent;
        transition: background 200ms;
      }
  
      .nav::-webkit-scrollbar-thumb {
        background: #a7bbeb;
      }
  
      .nav::-webkit-scrollbar-corner {
        background: none;
      }
  
      .nav:hover {
        scrollbar-color: #a7bbeb #2c5bd0;
      }
  
      .nav:hover::-webkit-scrollbar-track {
        background: #2c5bd0;
      }
    }
  
    .nav__list {
      display: flex;
      flex-flow: column nowrap;
      list-style: none;
      margin: 0;
      padding: 0;
      min-width: 2.5rem;
    }
  
    .nav__divider {
      margin: 1rem 0;
      padding: 0 0.625rem;
      min-width: 2.5rem;
    }
  
    .nav__divider::before {
      display: block;
      content: '';
      height: 0.99px;
      background: #fff;
    }
  
    .nav__item {
      position: relative;
    }
  
    .nav__item+.nav__item {
      margin-top: 0.25rem;
    }
  
    .nav__button {
      display: inline-flex;
      flex-flow: row nowrap;
      align-items: center;
      padding: 0;
      background: none;
      color: #a7bbeb;
      border: 0;
      overflow: hidden;
      border-radius: 0.5rem;
      cursor: pointer;
      outline-offset: -2px;
      transition-property: background, color;
      transition-duration: 250ms;
      max-width: 100%;
      width: 100%;
    }
  
    .is-active .nav__button {
      color: #fff;
    }
  
    .nav__button:focus-visible {
      outline: 2px solid #2c5bd0;
    }
  
    .nav__button:focus-visible,
    .nav__button:hover {
      background: #ffffff2f;
      color: #fff;
    }
  
    .nav__icon {
      height: 2.5rem;
      width: 2.5rem;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      flex: 0 0 auto;
    }
  
    .nav__icon .fa {
      aspect-ratio: 1;
      font-size: 18px;
    }
  
    .nav__label {
      display: inline-block;
      flex: 1;
      text-align: left;
      white-space: nowrap;
      transition-property: opacity, transform;
      transition-duration: 250ms;
      transition-timing-function: ease-out;
      transition-delay: 100ms;
      font-size: clamp(1rem, calc(0.75rem + 0.1vw), 1.25rem);
    }
  
    .is-collapsed .nav__label {
      opacity: 0;
      transform: translateX(1rem);
      transition-delay: 0s;
    }
  
    .nav__badge {
      border-radius: 50vh;
      background: #bafd5c;
      color: #000;
      font-size: 0.75rem;
      padding: 0.125rem 0.25rem;
      pointer-events: none;
      position: absolute;
      z-index: 1;
      right: 0;
      top: 50%;
      transform: translate(-0.25rem, -50%);
      transition-property: transform, font-size, padding, color;
      transition-duration: 250ms;
      transition-timing-function: ease-out;
    }
  
    .nav__badge.warn {
      background: #ffbc00;
    }
  
    .is-collapsed .nav__badge {
      transform: translate(-0.25rem, 50%);
      color: transparent;
      font-size: 0;
      padding: 0.25rem;
    }
  
    .sidenav__footer {
      margin-top: 1rem;
    }
  
    .user__button {
      display: inline-flex;
      flex-flow: row nowrap;
      align-items: center;
      padding: 0;
      background: none;
      color: #a7bbeb;
      border: 0;
      overflow: hidden;
      border-radius: 0.5rem;
      cursor: pointer;
      outline-offset: -2px;
      transition-property: background, color;
      transition-duration: 250ms;
      border-radius: 50vh;
      width: 100%;
      max-width: 100%;
      color: #fff;
    }
  
    .is-active .user__button {
      color: #fff;
    }
  
    .user__button:focus-visible {
      outline: 2px solid #2c5bd0;
    }
  
    .user__button:focus-visible,
    .user__button:hover {
      background: #0d43c9;
      color: #fff;
    }
  
    .user__badge {
      flex: 0 0 auto;
      width: 2.5rem;
      height: 2.5rem;
      padding: 2px;
    }
  
    .user__image {
      width: 100%;
      height: 100%;
      border-radius: 100%;
      background: #CFD8DC;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  
    .user__image .fa {
      color: #263238;
      font-size: 1.5rem;
    }
  
    .user__text {
      display: inline-block;
      flex: 1;
      text-align: left;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      transition-property: opacity, transform;
      transition-duration: 250ms;
      transition-timing-function: ease-out;
      transition-delay: 100ms;
      margin-left: 0.5rem;
      display: flex;
      flex-flow: column nowrap;
    }
  
    .is-collapsed .user__text {
      opacity: 0;
      transform: translateX(1rem);
      transition-delay: 0s;
    }
  
    .user__name,
    .user__title {
      overflow: hidden;
      text-overflow: ellipsis;
    }
  
    .user__title {
      font-size: 0.75rem;
      color: #a7bbeb;
    }
  
  }

  /* Media Querry FOr Side Nav */
@media screen and (max-width: 992px) {
  .seller-sidebar-glc {
    display: none;
  }
}