.restricted-margin {
  margin-top: 0;
}
 
 
 
 
 
/* Wrapper and dropdown button */
.menu-wraper {
  position: relative;
  display: inline-block;
}
 
.explore-btn {
  padding: 10px 20px;
  background-color: #333;
  color: #fff;
  cursor: pointer;
  font-weight: 500;
  border-radius: 4px;
}
 
.explore-btn:hover {
  background-color: #555;
}
 
/* Dropdown menu content */
 
.main-category {
  list-style: none;
  margin: 0;
  padding: 0;
}
 
.main-category li {
  position: relative;
}
 
.main-category li a {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  text-decoration: none;
  color: #333;
  font-size: 14px;
  transition: background 0.3s;
}
 
.main-category li a:hover {
  background-color: #f0f0f0;
  color: #000;
}
 
.main-category li i {
  margin-right: 10px;
  font-size: 16px;
}
 
/* Add border after first li */
.main-category li+li {
  border-top: 1px solid #e0e0e0;
}
 
.menu-content {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #fff;
  min-width: 250px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  z-index: 9999 !important;
  padding: 10px 0;
  border-radius: 6px;
}
 
/* Show menu on hover */
.menu-wraper:hover .menu-content {
  display: block;
}
 
.main-category li+li {
  border-top: 1px solid #e0e0e0;
  /* Light gray line between items */
}
 
/* Submenu (initially hidden) */
.submenu {
  display: none;
  position: absolute;
  top: 0;
  left: 100%;
  background: #fff;
  min-width: 220px;
  padding: 10px 0;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
}
 
/* Open class for submenu toggle */
.submenu.open-sub {
  display: block;
}
 
/*  */
 
@media screen and (max-width: 1200px) {
  .restricted-margin {
    margin-top: -120px !important;
  }
}